<template>
  <section>
    <figure>
      <img :src="front.screen" :title="front.title" :alt="front.title" />
    </figure>
    <article>
      <h2>{{ front.title }}</h2>
      <p>{{ front.description }}</p>
      <div>
        <p>
          <a :href="front.url" target="_blank" class="btn btn-primary"
            ><i class="fas fa-external-link-alt"></i> Ver Projeto</a
          >
          <a @click="labScroll()" class="btn btn-secondary"
            ><i class="fas fa-arrow-circle-left"></i> Voltar</a
          >
        </p>
      </div>
    </article>
  </section>
</template>

<script>
export default {
  name: "FrontendDetail",

  created() {
    this.front = this.$route.params.data;
  },
  methods: {
    labScroll() {
      window.history.back();
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
  },
};
</script>



<style lang="scss" scoped>
section {
  display: grid;
  grid-template-columns: 350px auto;
  // align-items: center;
  margin: 25px;
  img {
    width: 100%;
    border: 1px solid #f0f0f0;
  }
  div {
    margin-top: 10px;
  }
  p {
    color: #999;
    font-weight: 400;
    line-height: 25px;
  }
}

@media (max-width: 1024px) {
  section {
    grid-template-columns: 1fr;
    margin: 0px;
    article {
      margin: 25px;
    }
  }
}
</style>